import React from 'react';
import MetaTags from 'react-meta-tags';

function Contact(props){

    return(
            <div className="container">
              <MetaTags>
                <title>AutoZone, Inc. | Contact Us</title>
                <meta name="description" content="Contact US" />
                <meta property="og:title" content="AutoZone, Inc. | Contact Us" />
              </MetaTags>
                <h3 className="center">Contact Us</h3>
                <div className="box contact">
                  <p>Use this form if you have a Store related question or comment.<br />Get Customer Service Help Or,<br /> you can reach us by<br /> phone at
                1-800-AUTOZONE (1-800-288-6966)</p>
                <img src="https://autozone.know-where.com/autozone/Icon/US.png" id="map" name="map" alt=""></img>
                </div>
            </div>
        )
}

export default Contact;